___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --oxford-blue: #0d1a30ff;
    --glaucous: #507dbcff;
    --powder-blue: #a1c6eaff;
    --columbia-blue: #bbd1eaff;
    --platinum: #dae3e5ff;
    --custom-blue: rgb(72, 105, 149);

    --dark-1: #08131d;
    --dark-2: #0f1d2c;
    --dark-3: #343c4a;
}

/* Apply to all scrollbars */
::-webkit-scrollbar {
    width: 10px; /* Width of the scrollbar */
    height: 10px; /* Height of the scrollbar (horizontal) */
}

/* Track (background) of the scrollbar */
::-webkit-scrollbar-track {
    background: transparent; /* Light gray background */
    border-radius: 10px; /* Rounded corners */
}

/* Handle (thumb) of the scrollbar */
::-webkit-scrollbar-thumb {
    background: var(--dark-3); /* Darker gray handle */
    border-radius: 10px; /* Rounded corners */
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555; /* Even darker gray on hover */
}

/* Chat Loading Animation */
.loading-indicator {
    display: flex;
    align-items: center;
    gap: 4px;
    font-size: 16px;
    font-weight: bold;
}

.loading-indicator span {
    width: 6px;
    height: 6px;
    background-color: #888;
    border-radius: 50%;
    display: inline-block;
    animation: typing 1.5s infinite ease-in-out;
}

.loading-indicator span:nth-child(1) {
    animation-delay: 0s;
}
.loading-indicator span:nth-child(2) {
    animation-delay: 0.2s;
}
.loading-indicator span:nth-child(3) {
    animation-delay: 0.4s;
}

@keyframes typing {
    0% { opacity: 0.3; transform: scale(0.8); }
    50% { opacity: 1; transform: scale(1); }
    100% { opacity: 0.3; transform: scale(0.8); }
}

/* Welcome message typewriter animation */
    #header {
        font-size: 24px;
        font-weight: bold;
        font-family: monospace;
        white-space: nowrap;
        overflow: hidden;
        border-right: 3px solid black; /* Cursor effect */
        display: inline-block;
        
        width: 22ch; /* Matches the number of characters */
        
        animation: typing 2s steps(22, end) forwards, blink 0.7s infinite;
    }

    /* Typewriting animation */
    @keyframes typing {
        from {
            width: 0;
        }
        to {
            width: 22ch; /* Number of characters */
        }
    }

    /* Blinking cursor effect */
    @keyframes blink {
        50% {
            border-color: transparent;
        }
    }`, "",{"version":3,"sources":["webpack://./src/assets/css/deco-style.css"],"names":[],"mappings":"AAAA;IACI,wBAAwB;IACxB,qBAAqB;IACrB,wBAAwB;IACxB,0BAA0B;IAC1B,qBAAqB;IACrB,gCAAgC;;IAEhC,iBAAiB;IACjB,iBAAiB;IACjB,iBAAiB;AACrB;;AAEA,4BAA4B;AAC5B;IACI,WAAW,EAAE,2BAA2B;IACxC,YAAY,EAAE,yCAAyC;AAC3D;;AAEA,wCAAwC;AACxC;IACI,uBAAuB,EAAE,0BAA0B;IACnD,mBAAmB,EAAE,oBAAoB;AAC7C;;AAEA,oCAAoC;AACpC;IACI,yBAAyB,EAAE,uBAAuB;IAClD,mBAAmB,EAAE,oBAAoB;AAC7C;;AAEA,oBAAoB;AACpB;IACI,gBAAgB,EAAE,8BAA8B;AACpD;;AAEA,2BAA2B;AAC3B;IACI,aAAa;IACb,mBAAmB;IACnB,QAAQ;IACR,eAAe;IACf,iBAAiB;AACrB;;AAEA;IACI,UAAU;IACV,WAAW;IACX,sBAAsB;IACtB,kBAAkB;IAClB,qBAAqB;IACrB,2CAA2C;AAC/C;;AAEA;IACI,mBAAmB;AACvB;AACA;IACI,qBAAqB;AACzB;AACA;IACI,qBAAqB;AACzB;;AAEA;IACI,KAAK,YAAY,EAAE,qBAAqB,EAAE;IAC1C,MAAM,UAAU,EAAE,mBAAmB,EAAE;IACvC,OAAO,YAAY,EAAE,qBAAqB,EAAE;AAChD;;AAEA,yCAAyC;IACrC;QACI,eAAe;QACf,iBAAiB;QACjB,sBAAsB;QACtB,mBAAmB;QACnB,gBAAgB;QAChB,6BAA6B,EAAE,kBAAkB;QACjD,qBAAqB;;QAErB,WAAW,EAAE,qCAAqC;;QAElD,iEAAiE;IACrE;;IAEA,0BAA0B;IAC1B;QACI;YACI,QAAQ;QACZ;QACA;YACI,WAAW,EAAE,yBAAyB;QAC1C;IACJ;;IAEA,2BAA2B;IAC3B;QACI;YACI,yBAAyB;QAC7B;IACJ","sourcesContent":[":root {\n    --oxford-blue: #0d1a30ff;\n    --glaucous: #507dbcff;\n    --powder-blue: #a1c6eaff;\n    --columbia-blue: #bbd1eaff;\n    --platinum: #dae3e5ff;\n    --custom-blue: rgb(72, 105, 149);\n\n    --dark-1: #08131d;\n    --dark-2: #0f1d2c;\n    --dark-3: #343c4a;\n}\n\n/* Apply to all scrollbars */\n::-webkit-scrollbar {\n    width: 10px; /* Width of the scrollbar */\n    height: 10px; /* Height of the scrollbar (horizontal) */\n}\n\n/* Track (background) of the scrollbar */\n::-webkit-scrollbar-track {\n    background: transparent; /* Light gray background */\n    border-radius: 10px; /* Rounded corners */\n}\n\n/* Handle (thumb) of the scrollbar */\n::-webkit-scrollbar-thumb {\n    background: var(--dark-3); /* Darker gray handle */\n    border-radius: 10px; /* Rounded corners */\n}\n\n/* Handle on hover */\n::-webkit-scrollbar-thu