// Reate component App
import React, { useState } from "react";

const App = () => {
  const [showPopup, setShowPopup] = useState(false);

  return (
    <div>
      <button onClick={() => setShowPopup(true)}>Open Profile</button>

      {showPopup && (
        <div className="popup-overlay">
          <div className="popup-content">
            <h2>Customize ChatGPT</h2>
            <label>Name:</label>
            <input type="text" placeholder="What should ChatGPT call you?" />

            <label>What do you do?</label>
            <input type="text" placeholder="Your job title" />

            <div className="popup-actions">
              <button onClick={() => alert("Profile saved!")}>Save</button>
              <button onClick={() => setShowPopup(false)}>Cancel</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default App;