    --platinum: #dae3e5ff;
    --custom-blue: rgb(72, 105, 149);

    --dark-1: #08131d;
    --dark-2: #0f1d2c;
    --dark-3: #343c4a;
}

body {
    margin: 0;
    font-family: "Inter", serif;
    display: flex;
    height: 100vh;
    background-color: var(--dark-3);
    color: rgb(210, 206, 206);
}
body.light-mode {
    background-color: var(--platinum);
    color: var(--glaucous);
}
.sidebar {
    background: linear-gradient(135deg, var(--dark-2) 0%, var(--dark-1) 100%);
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.1);
    width: 250px;
    height: 100%;
    overflow-y: auto;
    position: relative;
    transition: width 0.5s ease; /*Transition Timer*/
}
.sidebar.light-mode {
    background: linear-gradient(135deg, var(--glaucous) 0%, var(--powder-blue) 100%);
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.1);
}

/* sidebar light mode */
.sidebar.light-mode h2 {
    background-color: var(--custom-blue); /* Light mode background color */
    color: var(--platinum); /* Optional: Adjust text color for contrast */
}

.sidebar.light-mode {
    color: var(--platinum); /* Set default text color for the sidebar in light mode */
}

.sidebar.light-mode ul li {
    color: var(--platinum); /* Default text color for list items */
}

.sidebar.light-mode ul li:hover {
    color: var(--glaucous); /* Hover color for list items */
    background-color: var(--columbia-blue); /* Optional hover background color */
}
    /* Light mode styles for Logout and Settings buttons */
    .sidebar.light-mode .logout button {
        background-color: var(--platinum); /* Background color for buttons */
        color: var(--oxford-blue); 
        /* border: 1px solid var(--platinum);  */
    }

    .sidebar.light-mode .logout button:hover {
        background-color: var(--custom-blue); /* Hover background color */
        color: var(--platinum); /* Hover text color */
    }

    .sidebar.sidebar.light-mode .logout button.logout-btn {
        color: rgb(191, 36, 36);
    }
    .sidebar.sidebar.light-mode .logout button.login-btn {
        color: rgb(53, 151, 66);
    }

    /* Light mode styles for the dropdown */
    body.light-mode .dropdown {
        background-color: var(--platinum); /* Light background color */
        color: var(--oxford-blue); /* Text color */
        border: 1px solid var(--glaucous); /* Optional border for better visibility */
        box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); /* Softer shadow */
    }

    body.light-mode .dropdown div {
        border-bottom: none; /* Divider color */
        color: var(--glaucous); /* Item text color */
    }

    body.light-mode .dropdown div:hover {
        border-radius: 8px;
        background-color: var(--columbia-blue); /* Hover background color */
        color: var(--glaucous); /* Hover text color */
    }
    body.light-mode .dropdown div:last-child {
        border-bottom: none;
    }


/* end sidebar light mode */

.sidebar.closed {
    width: 0;
    overflow: hidden;
}
.sidebar h2 {
    margin: 0;
    padding: 1rem;
    background-color: #1a2c3d;
    text-align: center;
}

/* Buttons in the header */
.search-button {
    position: absolute;
    top: 10px;
    left: 150px;
    margin-right: 5px;
    padding: 10px;
    background: transparent;
    color: white;
    position: auto;
    border: none;
    border-radius: 12px;
    cursor: pointer;
}
    .search-button:hover {
        background: #444;
    }
    body.light-mode .search-button:hover {
        background: var(--columbia-blue);
        color: var(--glaucous);
    }

.new-chat-button {
    position: absolute;
    top: 10px;
    left: 200px;
    margin: 0;
    padding: 10px;
    background: transparent;
    color: white;
    position: auto;
    border: none;
    border-radius: 12px;
    cursor: pointer;
}
    .new-chat-button:hover {
        background: #444;
    }
    body.light-mode .new-chat-button:hover {
        background: var(--columbia-blue);
        color: var(--glaucous);
    }

.sidebar button i{
    font-size: 1.2rem;
}
.sidebar button:hover {
    /* transform: scale(1.1); */
    color: var(--powder-blue); /* Optional: Highlight color */
}

.sidebar h2.light-mode {
    background-color: var(--platinum);
}
.sidebar ul {
    list-style-type: none;
    padding: 0;
    margin: 60px 0 0 0; /* Add 20px space above the list */
    height: auto; /* Optional: Ensure height is not constrained */
    overflow-y: auto;
}
.sidebar ul li {
    padding: 10px 20px;
    cursor: pointer;
    margin-bottom: 10px;
}
.sidebar ul li:hover {
    background-color: #1a2c3d;
    border: var(--powder-blue);
    border-radius: 10px;
}
.logout {
    display: flex;
    padding: 7px;
    position: absolute;
    bottom: 10px;
    width: auto;
    text-align: center;
    justify-content: center;
    gap: 5px;
    flex-wrap: wrap;
}
.logout button {
    flex: 1; /*Make sure both buttons take equal space*/
    background-color: #444;
    border: none;
    padding: 10px 20px;
    border-radius: 12px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}
.logout button i {
    margin-right: 8px;
}
.logout button.logout-btn {
    color: red;
}
.logout button.login-btn {
    color: rgb(24, 216, 24);
}
.logout button.settings-btn {
    color: white;
}
.logout button:hover {
    background-color: #555;
}
.dropdown {
    position: absolute;
    bottom: 60px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #333;
    color: white;
    border-radius: 8px;
    display: none;
    flex-direction: column;
    min-width: 150px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}
.dropdown.active {
    display: flex;
}
.dropdown div {
    padding: 10px;
    text-align: left;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 10px;
    border-bottom: none;
}
.dropdown div i {
    font-size: 1rem;
}
.dropdown div:hover {
    border-radius: 8px;
    background-color: #444;
}
.dropdown div:last-child {
    border-bottom: none;
}
/* Profile Dropdown */
/* Profile Button Styling */
.profile-btn {
    background: var(--platinum, #f0f0f0);
    padding: 10px 15px;
    border: none;
    cursor: pointer;
    position: relative;
    font-size: 16px;
    border-radius: 5px;
    transition: all 0.3s ease-in-out;
}

.profile-btn:hover {
    background: #dcdcdc;
    transform: scale(1.1);
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
}

/* Profile Dropdown Styling (Top Right Corner) */
.profileDropdown {
    position: absolute;
    top: 60px; /* Adjusted to drop from the top right */
    right: 4%; /* Adjusted to align with the right edge */
    background-color: #333;
    color: white;
    border-radius: 8px;
    display: none;
    flex-direction: column;
    min-width: 180px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}

.profileDropdown.active {
    display: flex;
}

