    padding: 10px 15px;
    text-align: left;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 10px;
    border-bottom: none;
    transition: background 0.3s ease;
}

.profileDropdown div i {
    font-size: 1rem;
}

.profileDropdown div:hover {
    border-radius: 8px;
    background-color: #444;
}

.profileDropdown div:last-child {
    border-bottom: none;
}

/* Light mode styles for the profileDropdown */
    body.light-mode .profileDropdown {
        background-color: var(--platinum); /* Light background color */
        color: var(--oxford-blue); /* Text color */
        border: 1px solid var(--glaucous); /* Optional border for better visibility */
        box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); /* Softer shadow */
    }

    body.light-mode .profileDropdown div {
        border-bottom: none; /* Divider color */
        color: var(--glaucous); /* Item text color */
    }

    body.light-mode .profileDropdown div:hover {
        border-radius: 8px;
        background-color: var(--columbia-blue); /* Hover background color */
        color: var(--glaucous); /* Hover text color */
    }
    body.light-mode .profileDropdown div:last-child {
        border-bottom: none;
    }
/* End Profile Dropdown */

.main {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.toggle-btn {
    position: absolute;
    top: 10px;
    left: 10px;
    background: var(--dark-2);
    border: none;
    color: white;
    padding: 10px;
    border-radius: 12px;
    cursor: pointer;
    z-index: 1000;
}
.toggle-btn i {
    font-size: 1.2rem;
}
.toggle-btn:hover {
    transform: scale(1.1); /* Slightly enlarge the button on hover */
    background: var(--dark-3);
}

.profile-btn {
    position: absolute;
    top: 10px;
    right: 20px;
    background: transparent;
    border: none;
    color: var(--glaucous);
    padding: 10px;
    border-radius: 50px;
    cursor: pointer;
    z-index: 1000;
}
.profile-btn i {
    font-size: 1.2rem;
}
.profile-btn:hover {
    background: var(--platinum);
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2); /* Soft shadow on hover */
}

.theme-toggle {
    display: flex;
    align-items: center;
    gap: 10px;
    justify-content: space-between;
}

.switch {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 24px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
    border-radius: 24px;
}

.slider:before {
    position: absolute;
    content: "";
    height: 18px;
    width: 18px;
    left: 3px;
    bottom: 3px;
    background-color: white;
    transition: .4s;
    border-radius: 50%;
}

input:checked + .slider {
    background-color: #0b3d6b;
}

input:checked + .slider:before {
    transform: translateX(26px);
}

/* Chat Area */
.chat-area.active {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    height: 680px; 
    padding: 5px;
    width: 100%;
    box-sizing: border-box;
}
.chat-messages.active {
    flex: 1; 
    width: 100%;
    max-width: 850px; /*Change to % if possible */
    overflow-y: auto;
    padding: 10px;
    border-radius: 10px;
    color: white;
    
    display: flex;
    flex-direction: column; /* Stack messages vertically */
    align-items: flex-start; /* Align messages to the left by default */
    gap: 10px; /* Add spacing between messages */
}
    /* body.light-mode .chat-messages.active {
        background-color: transparent;
        box-shadow: none;
    } */

    /* Chat message alignment */

    /* User message styling */
    .user-message {
        display: inline-block;
        background-color: var(--glaucous);
        color: white;
        padding: 12px 12px;
        border-radius: 15px;
        word-wrap: break-word; /* Ensure long words break to the next line */
        text-align: left; /* Keep text aligned, Text inside a message box */
        max-width: 70%; /* Prevent messages from being too wide */
        margin-left: auto; /* Ensure alignment to the right */
        margin-bottom: 10px; /* Space between messages */
        
    }

    /* Bot message styling */
    .bot-message {
        display: inline-block; /* Wrap background tightly around content */
        background-color: transparent;
        color: var(--platinum);
        padding: 12px 12px;
        border-radius: 15px;
        word-wrap: break-word; /* Ensure long words break to the next line */
        text-align: left; /* Keep text aligned */
        max-width: 70%; /* Prevent messages from being too wide */
        margin-right: auto; /* Ensure alignment to the left */
        margin-bottom: 10px; /* Space between messages */
        line-height: 1.8; /* Increase line height for better readability */
    }
    .light-mode .bot-message {
        color: var(--dark-1);
    }
    /* End Chat message alignment */

/* End Chat Area */
.chat-box {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 5px auto; /* Center horizontally */
    padding: 10px;
    /* padding-bottom: 50px; */
    border-radius: 25px; /* Rounded corners */
    background-color: #0f1d2c;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2); /* Optional shadow for depth */
    width: 80%; /* Adjust width as needed */
    max-width: 600px;
    
    position: relative; /* Default positioning */
    transform: translateY(0); /* Starting point */
    
    transition: top 0.5s ease-in-out; /* Smooth transition for top movement */
}
/* When click send, chat box moves to bottom */
    /* Define the animation */
    .chat-box.active {
        position: relative;
    }

.chat-input {
    flex: 1;
    padding: 10px;
    border: none;
    border-radius: 20px;
    outline: none;
    background-color: #343c4a;
    color: white;
    font-size: 1rem;
}

.text-input {
    flex: 1;
    padding: 25px;
    border: none;
    border-radius: 20px;
    outline: none;
    background-color: #343c4a;
    color: white;
    font-size: 1rem;
    resize: none; /* Prevent manual resizing */
    overflow: hidden; /* Hide scrollbar */
    line-height: 1.5; /* Improve readability */
    min-height: 40px; /* Set a minimum height */
}

.chat-input::placeholder {
    color: #bbb; /* Placeholder text color */
}

.send-button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
}

.send-button .icon {
    width: 30px;
    height: 30px;
    transition: transform 0.2s ease;
}

.send-button:hover .icon {
    transform: scale(1.1); /* Slightly enlarge the icon on hover */
}

/* Plus icon style */
.plus-icon {
    font-size: 24px;
    font-weight: bold;
    color: white;
}

#modelIcon {
    filter: invert(50%) sepia(100%) saturate(500%) hue-rotate(180deg); /* Example filter to change color */
}


/* Chat Box Light Mode */
    /* Light mode styles for chat box */
    body.light-mode .chat-box {
        background-color: var(--powder-blue); /* Light background color */
        box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); /* Softer shadow */
    }

    body.light-mode .chat-input {
        background-color: var(--columbia-blue); /* Input field background */
        color: var(--oxford-blue); /* Text color */
    }

    body.light-mode .text-input {
        background-color: var(--columbia-blue); /* Input field background */
        color: var(--oxford-blue); /* Text color */
    }

    body.light-mode .chat-input::placeholder {
        color: var(--glaucous); /* Placeholder text color */
    }

    body.light-mode .send-button .icon {
        filter: brightness(1); /* Adjust icon brightness for light mode */
    }

/* Media queries for responsiveness */
@media (max-width: 768px) {
    .sidebar {
        width: 250px; /* Adjust sidebar width for smaller screens */
    }

    .toggle-btn {
        top: 5px;
        left: 5px;
        padding: 8px;
    }

    .profile-btn {
        top: 5px;
        right: 10px;
        padding: 8px;
    }

    .search-button, .new-chat-button {
        top: 5px;
        left: 100px;
        padding: 8px;
    }

    .chat-area.active {
        height: 500px; /* Adjust chat area height for smaller screens */
    }

    .chat-box {
        width: 90%; /* Adjust chat box width for smaller screens */
        max-width: 100%; /* Ensure it doesn't exceed screen width */
    }

    .chat-messages.active {
        max-width: 100%; /* Ensure it doesn't exceed screen width */
    }

    .user-message, .bot-message {
        max-width: 90%; /* Adjust message width for smaller screens */
    }
}

@media (max-width: 480px) {
    .sidebar {
        width: 300px;
    }

    .toggle-btn {
        top: 3px;
        left: 3px;
        padding: 6px;
    }

    .profile-btn {
        top: 3px;
        right: 5px;
        padding: 6px;
    }

    .search-button, .new-chat-button {
        top: 3px;
        left: 80px;
        padding: 6px;
    }

    .chat-area.active {
        height: 400px; /* Further adjust chat area height for very small screens */
    }

    .chat-box {
        width: 95%; /* Further adjust chat box width for very small screens */
        max-width: 100%; /* Ensure it doesn't exceed screen width */
    }

    .chat-box.active {
        top: 0; /* Move chat box to the top */
    }

    .chat-messages.active {
        max-width: 100%; /* Ensure it doesn't exceed screen width */
    }

    .user-message, .bot-message {
        max-width: 85%; /* Further adjust message width for very small screens */
    }
